/**
 * AI Commerce SDK
 * 
 * AI-powered product recommendations for e-commerce
 * 
 * @packageDocumentation
 * @module @yassirbenmoussa/aicommerce-sdk
 * 
 * @example
 * ```typescript
 * // npm usage - Client API
 * import { AICommerce } from '@yassirbenmoussa/aicommerce-sdk';
 * 
 * const client = new AICommerce({ apiKey: 'your-api-key' });
 * const response = await client.chat('I need a laptop');
 * console.log(response.products);
 * ```
 * 
 * @example
 * ```html
 * <!-- Widget usage -->
 * <script src="https://cdn.aicommerce.dev/widget.min.js"></script>
 * <script>
 *   AICommerceWidget.init({
 *     apiKey: 'your-api-key',
 *     position: 'bottom-right',
 *     theme: 'auto'
 *   });
 * </script>
 * ```
 */

// Export main client
export { AICommerce, AICommerceError } from './client';

// Export widget
export { createWidget, AICommerceWidget } from './widget';

// Export all types
export type {
    // Client types
    AICommerceConfig,
    ChatRequest,
    ChatResponse,
    ChatContext,
    Product,
    Session,
    APIError,
    EventType,
    EventCallback,
    // Widget types
    StoreConfig,
    WidgetConfig,
    WidgetInstance,
} from './types';

// Version
export const VERSION = '1.0.0';

// For UMD builds, attach to window
if (typeof window !== 'undefined') {
    // @ts-expect-error - Attaching to window for UMD
    window.AICommerce = require('./client').AICommerce;
    // @ts-expect-error - Attaching to window for UMD
    window.AICommerceError = require('./client').AICommerceError;
    // @ts-expect-error - Attaching to window for UMD
    window.AICommerceWidget = require('./widget').AICommerceWidget;
}
